/* Estilos para la checkbox */
#mi-checkbox {
  display: none; /* Oculta la checkbox por defecto */
}

/* Estilos para el contenedor de la checkbox */
label[for="mi-checkbox"] {
  display: inline-block;
  position: relative;
  padding-left: 25px; /* Espacio para el indicador */
  cursor: pointer;
}

/* Estilos para el indicador */
label[for="mi-checkbox"]:before {
  content: "";
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  border: 1px solid #000;
  background-color: #fff;
}

/* Estilos para el indicador cuando está marcado */
#mi-checkbox:checked + label[for="mi-checkbox"]:before {
  background-color: #000;
}

/* Estilos para el indicador marcado */
#mi-checkbox:checked + label[for="mi-checkbox"]:after {
  content: "";
  display: inline-block;
  position: absolute;
  left: 6px;
  top: 6px;
  width: 8px;
  height: 8px;
  background-color: #000;
}

